      <h1 class="h1">Responsive Relief - An Interactive Machine for Space</h1>
      <p class="p1">
        With a reflection on the power of an architect to influence and produce space, we are inspired by Tinguely and
        interactive Kinetic Art he produced to design our own interactive machine that allows the user to create, in
        real time, spatial / geometric correlations based on specific parameters.
      </p>
      <img class="img3" src="../../../images/Architecture/projects/7interaction/3.jpg" alt="" />
