      <p class="p2">
        A grid of nine points, which move vertically by means of gears and vertical guides, gives the desired visual
        effect. The nine pipes are held through three levels. The first level is the base of the machine, the second
        secures the vertical guide mechanism and the third is holding the plastic tubes.
      </p>

      <p class="p3">
        The transformations of the grid’s height are the result of the rotation of the values of light/shadow received
        by the photoresistors on top of the nine tubes. The user can alter these values with his fingers, each time
        creating the result of their choice. The machine will return to its original state when it no longer receives
        alterations from the photoresistors. Thus, the next user can create his own result, in a repeatable dynamic
        process of producing elevation relations on the responsive relief.
